					this.$router.push({
						path:'/mains/print',
						query:{List:this.multipleSelection1}
					})
				}else{
					this.$message({
						message: '',
						type: 'warning'
					});
				}
			},
			//
			tableRowClassName({row, rowIndex}) {
				if (row.State == -1) {
					return 'warning-row'
				} else if (row.State == 1) {
					return 'success-row';
				}else if(row.State == -2){
					return 'quxiao-row';
				}
				return '';
			},
			tableRowClassName1({row, rowIndex}) {
				if (row.State == -1) {
					return 'warning-row'
				} else if (row.State == 1) {
					return 'success-row';
				}else if(row.State == -2){
					return 'quxiao-row';
				}
				return '';
			},
      		//
			handleSelectionChange(val) {
				this.multipleSelection = val;
				console.log(this.multipleSelection)
			},
			handleSelectionChange1(val) {
				this.multipleSelection1 = val;
				console.log(this.multipleSelection1)
			},
			//
			openInput(tab, event) {
				this.$router.push('/mains/input')
				sessionStorage.setItem('inputValue', JSON.stringify(event))
			},
			//
			searchClick(){
				this.getTableData()
			},
			searchClick1(){
				this.getTableData1()
			},
			//
			timeChange(){
				if(this.timeValue){
					this.StartDate = this.timeValue[0]
					this.EndDate = this.timeValue[1]
				}else{
					this.StartDate = ''
					this.EndDate = ''
				}
				this.getTableData()
			},
			timeChange1(){
				if(this.timeValue1){
					this.StartDate1 = this.timeValue1[0]
					this.EndDate1 = this.timeValue1[1]
				}else{
					this.StartDate1 = ''
					this.EndDate1 = ''
				}
				this.getTableData1()
			},
			//
			uploadSuccess(){
				console.log(this.allUpload)
			},
			//
			handleChange(file, fileList) {
				this.fileTemp = file.raw;
				if (this.fileTemp) {
					if ((this.fileTemp.type == 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet') || (this.fileTemp.type ==
							'application/vnd.ms-excel')) {
						this.importfxx(this.fileTemp);
					} else {

						this.$message({
							type: 'warning',
							message: ''
						})
					}
				} else {
					this.$message({
						type: 'warning',
						message: ''
					})
				}
			},
			//
			handleExceed() {
				this.$message({
					type: 'warning',
					message: ''
				})
				return;
			},
			//
			handleRemove(file, fileList) {
				this.fileTemp = null
			},
			// excel
			importfxx(obj) {
				let _this = this;
				let inputDOM = this.$refs.inputer; // DOM
				this.file = event.currentTarget.files[0];
				var rABS = false; //
				var f = this.file;
				var reader = new FileReader();
				FileReader.prototype.readAsBinaryString = function(f) {
					var binary = "";
					var rABS = false; //
					var pt = this;
					var wb; //
					var outdata;
					var reader = new FileReader();
					reader.onload = function(e) {
						var bytes = new Uint8Array(reader.result);
						var length = bytes.byteLength;
						for (var i = 0; i < length; i++) {
							binary += String.fromCharCode(bytes[i]);
						}
						//main.jsexcel
						var XLSX = require("xlsx");
						if (rABS) {
							wb = XLSX.read(btoa(fixdata(binary)), { //
								type: "base64"
							});
							console.log(wb)
						} else {
							wb = XLSX.read(binary, {
								type: "binary"
							});

						}
						outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]); //outdata
						//
						let arr = [];
						outdata.map(v => {
							let obj = {}
							obj.DateID = v['DateID']
							obj.PictureID = v['PictureID']
							obj.Date = v['Date']
							obj.Name = v['Name']
							obj.IDCardNo = v['IDCardNo']
							obj.Sex = v['Sex']
							obj.Age = v['Age']
							obj.Nation = v['Nation']
							obj.Schooling = v['Schooling']
							obj.UnitProperty = v['UnitProperty']
							obj.UnitName = v['UnitName']
							obj.WorkType = v['WorkType']
							obj.WorkTime = v['WorkTime']
							obj.PhoneNo = v['PhoneNo']
							arr.push(obj)
						});
						_this.tableDatas = arr;
						return arr;
					};
					reader.readAsArrayBuffer(f);
				}
				if (rABS) {
					reader.readAsArrayBuffer(f);
				} else {
					reader.readAsBinaryString(f);
				}
				this.tableDatas = _this.tableDatas;
				setTimeout(()=>{
					this.ReserveBatch()					
				},500)
			}
		}
	};
</script>


